.layer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0,.6);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;    
    .close {
        position: absolute;
        right: 40px;
        top: 40px;
        img {
            width: 40px;
            color: #fff;
            font-weight: bold;
            font-size: 20px;
        }
    }
}