  html {
      overflow: auto;
  }

  html,
  body {
      height: 100%;
      background-color: #fff;
      scroll-behavior: smooth;
  }

  #root {
      min-width: 1200px;
  }

  .out {
      background-color: #fff;
      margin: 0 auto;
      margin-right: 0;
        padding-top: 41px;
      &.show-aside {
          margin-left: 460px;
          margin-right: -460px;
      }
  }

  .vid-wrapper {
      width: 100%;
      position: relative;
      height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .vid-wrapper video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      outline: none;
      border: none;
  }

  .vid-wrapper-out {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, .8);
      z-index: 200;
      box-sizing: border-box;
      z-index: -1000;
      transition-property: opacity;
      transition-duration: 500ms;
      left: -10000px;
  }

  .home {
      height: 678px;
      background-image: url('./images/home-bg.png');
      background-repeat: no-repeat;
      background-size: 50% 100%;
      background-color: #f2f2f3;
      text-align: center;
      box-sizing: border-box;
      padding-top: 100px;
        background-size: auto 100%!important;
      .home-logo {
          max-width: 462px;
          width: 25.52%;
          margin-bottom: 57px;
      }

      .home-title {
          display: block;
          margin-bottom: 57px;
          margin-left: auto;
          margin-right: auto;
          max-width: 630px;
          width: 32.81%;
          height: auto;
      }

      .home-text {
          margin: 0 auto;
          max-width: 960px;
          line-height: 32px;
          font-size: 18px;
          font-weight: 400;
          letter-spacing: 4px;
      }
  }

  .home,
  .pixure,
  .size-finder,
  .w-wardrobe,
  .section-5,
  .we-pattern,
  .join-us {
      position: relative;
      background-size: cover;
  }

  .pixure {
      height: 960px;
      background-image: url('./images/section-2.png');
  }

  .pixure .pixure-list {
      position: absolute;
      list-style: none;
      display: flex;
      left: 50%;
      margin-left: 110px;
      bottom: 115px;
      font-size: 18px;

      .pixure-list-item {
          color: #fff;
          cursor: pointer;
          font-weight: bold;

          &:not(:first-of-type) {
              margin-left: 60px;
          }
      }
  }

  .pixure .pixure-outputs {
      position: absolute;
      bottom: 190px;
      left: 50%;
      margin-left: 110px;
      list-style: none;
      color: #fff
  }

  .pixure .pixure-outputs-item {
      line-height: 32px;
      font-size: 24px;
  }

  .size-finder {
      height: 960px;
      background-image: url('./images/section-3.png');
      padding-top: 860px;
      box-sizing: border-box;
  }

  .size-finder .apply-now {
      font-size: 24px;
      text-align: center;
      cursor: pointer;
  }

  .w-wardrobe {
      height: 920px;
      background-image: url('./images/section-4.png');
      box-sizing: border-box;
      padding-top: 830px;
      .w-wardrobe-list {
          text-align: center;
          font-size: 16px;
      }

      .w-wardrobe-list-item {
          display: inline-block;
          margin-right: 40px;
          margin-left: 40px;
          font-size: 24px;
      }
  }

  .we-pattern {
      height: 2020px;
      background-image: url('./images/section-6.png');
      padding-top: 1277px;
      box-sizing: border-box;

      .intro {
          text-align: center;
          color: #fff;
          line-height: 46px;
          font-size: 28px;
          font-weight: 400;
          letter-spacing: 4px;
          .intro-tip {
              text-align: center;
          }
      }

      .intro-join {
          margin-top: 90px;
          text-align: center;
          font-size: 30px;
      }

      .fixed-hezuo {
          position: absolute;
          top: 900px;
          left: 50%;
          margin-left: 380px;
          width: 260px;
          transform: translate(-50%, -50%);

          img {
              width: 100%
          }
      }

  }

  .join-us {
      height: 1170px;
      background-image: url('./images/section-7.png');
      padding-top: 148px;
      box-sizing: border-box;

      .texts {
          padding-bottom: 300px;
      }

      .texts p {
          text-align: center;
          line-height: 50px;
          font-size: 28px;
          font-weight: 400;
          letter-spacing: 4px;
      }

      .copyright {

          p {
              text-align: center;
              line-height: 32px;
              font-size: 18px;
              color: #666;
          }
      }

      .qrcodes {
          position: absolute;
          width: 100%;
          bottom: 0;
          display: flex;
          justify-content: center;
          padding-bottom: 20px;
      }

      .qrcodes figure {
          margin: 0;
          margin-left: 28px;
          margin-right: 28px;
          text-align: center;
      }

      .qrcodes figure img {
          width: 100px;
          height: 100px;
          margin-bottom: 6px;
      }

      .qrcodes figure figcaption {
          text-align: center;
          font-size: 24px;
      }
  }